/* eslint-disable react/jsx-props-no-spreading */
import React, {
    useState,
    FunctionComponent,
    useMemo,
    useCallback,
} from 'react';
import {
    JsonGroup,
    Config,
    ImmutableTree,
    BuilderProps,
    Query,
    Builder,
    Utils as QbUtils,
    JsonLogicTree,
    Fields,
} from '@react-awesome-query-builder/mui';
import { MESSAGES } from '../messages';

import { useTranslatedConfig } from '../hooks/useTranslatedConfig';

import { useStyles } from '../styles';
import { useSafeIntl } from '../../../utils/useSafeIntl';

type Props = {
    logic?: JsonLogicTree;
    fields: Fields;
    onChange: (
        // eslint-disable-next-line no-unused-vars
        jsonLogic: JsonLogicTree,
    ) => void;
    // those two fields are used to set the value of date or datetime while selecting current date or current date time
    // required if CurrentDate and CurrentDatetime are used instead of date or datetime
    currentDateString?: string;
    currentDateTimeString?: string;
};

const queryValue: JsonGroup = { id: QbUtils.uuid(), type: 'group' };

export const QueryBuilder: FunctionComponent<Props> = ({
    logic,
    fields,
    onChange,
    currentDateString,
    currentDateTimeString,
}) => {
    const {formatMessage} = useSafeIntl();
    const extendedFields: Fields = useMemo(() => ({
        ...fields,
        ...(currentDateString
            ? {
                [currentDateString]: {
                    label: formatMessage(MESSAGES.currentDate),
                    type: "currentDate",
                    valueSources: ['value', 'field'],
                }
            }
            : {}),
        ...(currentDateTimeString
            ? {
                [currentDateTimeString]: {
                    label: formatMessage(MESSAGES.currentDateTime),
                    type: "currentDatetime",
                    valueSources: ['value', 'field'],
                }
            }
            : {}),
    }), [fields, currentDateString, currentDateTimeString]);

    const translatedConfig = useTranslatedConfig(
        currentDateString,
        currentDateTimeString,
    );

    const config: Config = useMemo(
        () => ({
            ...translatedConfig,
            fields: extendedFields,
        }),
        [extendedFields, translatedConfig],
    );
    const [tree, setTree] = useState(
        QbUtils.checkTree(
            QbUtils.loadFromJsonLogic(logic, config) ||
                QbUtils.loadTree(queryValue),
            config,
        ),
    );

    const handleChange = useCallback(
        (immutableTree: ImmutableTree, newConfig: Config) => {
            setTree(immutableTree);
            onChange(QbUtils.jsonLogicFormat(immutableTree, newConfig));
        },
        [onChange],
    );

    const renderBuilder = useCallback((props: BuilderProps) => {
        return (
            <div
                className="query-builder-container"
                style={{ padding: '10px' }}
            >
                <div className="query-builder qb-lite">
                    <Builder {...props} />
                </div>
            </div>
        );
    }, []);

    const classes: Record<string, string> = useStyles();

    return (
        <section className={classes.root}>
            <Query
                {...config}
                value={tree}
                onInit={handleChange}
                onChange={handleChange}
                renderBuilder={renderBuilder}
            />
        </section>
    );
};
